import * as React from 'react'

/* --
Button types
- primary
- primary-alt
- secondary
- secondary-alt

-- */

const Button = ({ text, type = 'primary', onClick, disabled, className}) => {
  return ((type === 'primary' &&
    <button className={`btn btn-primary ${className}`} onClick={onClick} disabled={disabled}>{text}</button>) ||
    (type === 'primary-alt' &&
    <button className={`btn btn-primary-alt ${className}`} onClick={onClick} disabled={disabled}>{text}</button>) ||
    (type === 'secondary' &&
    <button className={`btn btn-secondary ${className}`} onClick={onClick} disabled={disabled}>{text}</button>) ||
    (type === 'secondary-alt' &&
    <button className={`btn btn-secondary-alt ${className}`} onClick={onClick} disabled={disabled}>{text}</button>)
  )
}

export default Button