import * as React from 'react'

const Circle = ({ emoj, title, body, className }) => {
  return (
    <div className='xs:w-1/2 md:w-1/3 flex-grow text-center'>
    <section className={`rounded-full border-4 sm:border-8 h-48 w-48 sm:h-52 sm:w-52 flex items-center px-5 py-3 my-3 mx-auto ${className}`}>
      <div>
        <span className="text-xl">{emoj}</span>
        <h3 className='text-base font-bold leading-tight mb-1'>{title}</h3>
        <p className='text-xs'>{body}</p>
      </div>
    </section>
    </div>
  )
}

export default Circle