import * as React from 'react'

const InputField = ({ label, name, type = 'text', className, value, onChange}) => {
  return (
    <div className={`relative md:mr-4 border-b-2 ${className}`}>
      <label className="absolute px-2 top-2 text-xs w-full">{label}</label>
      <input type={type} value={value} onChange={onChange} name={name}
        className={`pt-6 pb-1 pl-2 bg-transparent text-black relative z-10 text-base w-full`} />
    </div>
  )
}

export default InputField