import React, { useState } from "react";
import InputField from "./InputField"
import Button from './Button'

const FormRegister = ({style = "light"}) => {
  const [formData, setFormData] = useState({
    firstName: "",
    emailAddress: "",
  });

  const [formStaus, setFormStaus] = useState() // disabled, sent

  const handleInputChange = (event) => {
    const { name, value } = event.target;

    // Update form data
    setFormData({
      ...formData,
      [name]: value,
    });
  }

  const handleSubmit = async (e) => {
    e.preventDefault();

    if (!formData.firstName) {
      return false
    }

    if (!formData.emailAddress) {
      return false
    }

    setFormStaus('disabled')

    try {
      const response = await fetch("https://happybytes.io/gift-reg-api/ft-register", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
        body: JSON.stringify(formData)
      });

      const result = await response.json()
      if (result) {
        setFormStaus('sent')
      }
    } catch(error) {
      console.error("Error:", error)
    }
  
  }

  return (
    formStaus === 'sent' ? <p className="text-center mt-4">Thanks for registering, keep an eye on your inbox for launch updates!</p> :
    <form className='md:flex justify-between mt-6 max-w-full w-[850px] mx-auto'>
      { style === 'light' ?
      <>
          <InputField label="First name" name="firstName" type="text" className="md:w-1/2 mb-4 border-ft-red-1"
        value={formData.firstName} onChange={handleInputChange} />
          <InputField label="Email address" name="emailAddress" type="email" className="md:w-1/2 mb-4 border-ft-red-1"
            value={formData.emailAddress} onChange={handleInputChange} />
          <Button text="Register" type="primary-alt" className="w-full md:w-auto mb-4" onClick={handleSubmit} disabled={formStaus === "disabled"} />
      </> :
      <>
        <InputField label="First name" name="firstName" type="text" className="md:w-1/2 mb-4 border-ft-papaya-1"
        value={formData.firstName} onChange={handleInputChange} />
        <InputField label="Email address" name="emailAddress" type="email" className="md:w-1/2 mb-4 border-ft-papaya-1"
          value={formData.emailAddress} onChange={handleInputChange} />
        <Button text="Register" type="secondary" className="w-full md:w-auto mb-4" onClick={handleSubmit} disabled={formStaus === "disabled"} />
      </>
      }
    </form>
  )
}

export default FormRegister